<template>
  <div>
    
    <table class="table m-0 table-top-line-none">
      <tbody class="listing-tbody">

        <tr v-for="(f, index) in _listingContent" :key="f.id" class="prices-item text-left" :class="{ inActive: !inList(f.id, _select), 'bg-white': inList(f.id, _select) }">
          <td width="6%" class="text-accent">{{ index + 1 }}</td>
          <td width="10%">{{ f.num }}</td>
          <td width="20%">{{ f.named }}</td>
          <td width="15%" class="table-input-td ">
            
            <div class="input-group input-group-sm float-left listing-num-input listing-input-sm">
              <input 
                type="text" 
                class="form-control"
                :id="'count_' + f.id" 
                :value="f.count" 
                @keyup.enter="changeCount(f.id, index)"
                @change="changeCount(f.id, index)"
              >
              <div class="input-group-append">
                <span class="input-group-text" id="basic-addon2">{{ unit(f.unit) }}</span>
              </div>
            </div>
          </td>
          <td width="15%" class="table-input-td">
            
            <div class="float-left mt-1 text-input">$</div>
            <div class="input-group input-group-sm float-left listing-num-input">
              <input 
                type="text" 
                class="form-control" 
                :id="'price_' + f.id" 
                :value="f.price" 
                @keyup.enter="changePrice(f.id, index)"
                @change="changePrice(f.id, index)"
              >
            </div>

          </td>
          <td width="9%">
            {{ f.zk }}
          </td>
          <td width="15%" class="text-center">
            {{ (f.count * f.price).toFixed(2)  }}
          </td>
          
          <td width="10%" class="text-center">
              
            <div v-show="inList(f.id, _select)">
              <i class="fa hand listing-table-icon fa-remove" @click="itemOption(f.id, 'remove')"></i>
            </div>
            <div v-show="!inList(f.id, _select)">
              <i class="fa hand listing-table-icon fa-plus" @click="itemOption(f.id, 'add')"></i>
            </div>
            
          </td>
        </tr>

      </tbody>
    </table>

  </div>
</template>

<script>
import $ from 'jquery'
import service from '@/service'
import view from '@/common/es/view'
import tool from '@/common/es/tool'

export default {
  name: 'ListingPlusBody',
  props: {
    _listingContent: {
      type: Array
    },
    _select: {
      type: Array
    }
  },
  mounted() {
    
  },  
  methods: {

    unit(u) {
      return view.ser_unit(u)
    },
    inList(n, L) {
      return tool.inList(n, L)
    },
    ser_timed(v) {
      return tool.timed.ser_timed(v, true, false)
    },

    // 數值控制
    changeCount(id, index) {
      const val = $('#count_' + id).val()
      this._listingContent[index].count = val

    },
    changePrice(id, index) {
      const val = $('#price_' + id).val()
      this._listingContent[index].price = val
    },

    itemOption(id, option) {
      if (option == 'add') { 
        this._select.push(id) 
      }
      else if (option == 'remove') { 
        const i = tool.indexList(id, this._select) 
        if (this._select.length < 2) {
          tool.showToast('至少選擇壹項菜品。')
        } else {
          this._select.splice(i, 1)
        }
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"
@import "@/common/sass/choise.sass"
@import "@/common/sass/content.sass"
</style>


