<template>
    <div class="form-group m-0">
      <label :for="'select_' + the_name" class="col-form-label p-0">
        <slot name="input_txt"></slot>
      </label>
      <div class="input-wrapper">
        
        <select class="form-control fs-14" :id="'select_' + the_name" :name="the_name"
          v-model="value" @change="valueSubmit"
        >
          <option v-for="(v, index) in the_list" :key="index" :value="index" >{{ v }}</option>
        </select>
      </div>
    </div>
</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'

export default {
  name: 'ListingTimedInoput',
  props: {
    the_name: {
      type: String
    },
    the_list: {
      type: Array // return index of data
    }
  },
  data() {
    return {
      value: 0
    }
  },
  methods: {
    tipToast(msg) {
      tool.showToast(msg)
    },
    valueSubmit() {
      this.$emit('returnValue', this.value)
    },
    locationData(v) {
      this.value = v
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.input-wrapper select
  width: 100%
  height: 24px
  border: none !important
  padding: $distance $horizonS
  padding-right: 0px

.input-wrapper
  display: inline-block
  width: 96px

</style>


