<template>  

<div class="modal fade" id="listingFullMsgModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
  <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
    <div class="modal-content">
      <div class="modal-header">
        <h5 class="modal-title">
          完善信息
          <div class="fs-12">
            提示: 發票信息完善後，才可提交
          </div>
        </h5>
      </div>
      <div class="modal-body">

        <form class="row">
          <div class="form-group col-sm-12">
            <label for="named">聯絡人</label>
            <input type="text" class="form-control" id="named" placeholder="名稱" v-model="named">
            <small id="namedTip" class="form-text text-muted">{{ tip_named }}</small>
          </div>
          <div class="form-group col-sm-12">
            <label for="contact_area">聯系電話</label>
            <div class="row">
              <div class="col-sm-4">
                <select class="form-control" id="contact_area" v-model="contact_area">
                  <option v-for="a in areas" :key="a.id" :value="a.id">{{ a.area }}</option>
                </select>
              </div>
              <div class="col-sm-8">
                <input type="text" class="form-control" id="contact_phoned" placeholder="電話" v-model="contact_phoned">
                <small id="contact_phonedTip" class="form-text text-muted">{{ tip_contact_phoned }}</small>
              </div>
            </div>
          </div>
        </form>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-secondary" data-dismiss="modal">關閉</button>
        <button type="button" class="btn btn-info" @click="listingPlusSubmit">提交</button>
      </div>
    </div>
  </div>
</div>

</template>

<script>
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'
import conn from '@/service/conn'

export default {
  name: 'ListingPlusFullMsg',
  data() {
    return {
      named: '',
      areas: [],

      contact_phoned: '',
      contact_area: '',

      tip_named: '',
      tip_contact_phoned: ''
    }
  },
  mounted() {

    conn.area.getList_Area({}, true).then(res => {
      this.areas = res
      if (this.contact_area == 0) { this.contact_area = res[0].id }
    })
  },
  methods: {
    listingPlusSubmit() {

    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


