<template>
  <div class="modal fade" id="pricesChoiseModal" data-backdrop="static" data-keyboard="false" tabindex="-1" role="dialog" aria-labelledby="staticBackdropLabel" aria-hidden="true">
    <div class="modal-dialog modal-lg modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title">
            選擇的報價單：
            <div class="fs-12">
              提示：需要選擇一份報價單才能開始製作發票
            </div>
          </h5>

          <listing-prices-card :_prices="pricesChoise" v-if="pricesChoise != null"></listing-prices-card>

        </div>
        <div class="modal-body pt-0 pb-0">
          
          <div class="">

            <table class="table table-top-line-none" v-if="pricesList.length > 0">
              <tr v-for="m in pricesList" :key="m.id" 
                :class="{ 'item-choise': m.id == pricesChoise.id }" 
                @click="choised(m.id)" class="hand">

                <td>{{ m.num }}</td>
                <td>{{ ser_timed(m.start_time) }}</td>
                <td>{{ m.membery.num }}&nbsp;{{ m.membery.named }}</td>
              </tr>
            </table>

          </div>

        </div>

        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" data-dismiss="modal">離開</button>
          <button type="button" class="btn btn-main" @click="choiseSubmit">確認選擇</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'

import ListingPricesCard from '../../Card/ListingPricesCard.vue'

export default {
  components: { ListingPricesCard },
  props: {
    defPrices: {
      type: Object,
      default() { return null }
    }
  },
  data() {
    return {
      pricesList: [ ],
      pricesChoise: null
    }
  },
  mounted() {
    conn.prices.getList_Prices().then(res => {
      if (this.defPrices == null) {
        this.loadFirstChoise(res[0])
      } else {
        this.loadFirstChoise(this.defPrices)
      }
      this.pricesList = res
    })
  },
  methods: {
    loadFirstChoise(v) {
      if (this.pricesChoise == null) { this.pricesChoise = v }
    },
    ser_timed(v) {
      return tool.timed.ser_timed(v, true, true)
    },
    choised(id) {
      for(const m of this.pricesList) {
        if(m.id == id) {
          this.pricesChoise = m
        }
      }
    },
    choiseSubmit() {

      if (this.pricesChoise == null) {
        tool.showToast('您还未选择一名客户')
      } else {
        this.$emit('returnChoised', this.pricesChoise)
        tool.closeModal('pricesChoiseModal')
      }
    }
  }
}
</script>
<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.members-search-panel, .member-table
  margin: 0 !important

.members-panel
  border-top: none

.member-table tr:first-child > td
  border: none


</style>


