<template>
  <div class="fs-12">
    <div class="float-left icon-wrapper">
      <i class="fa fa-list-alt"></i>
    </div>
    <div class="float-left ml-2">
      <div>
        單號：{{ _prices.num }}&nbsp;
        <i class="fa fa-edit text-primary hand" v-if="is_icon" @click="changeSomething"></i>
      </div>
      <div>
        所屬客戶：{{ _prices.membery.num }}&nbsp;{{ _prices.membery.named }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListingPricesCard',
  props: {
    _prices: {
      type: Object
    },
    is_icon: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    changeSomething() {
      this.$emit('changeSignal')
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

.icon-wrapper i
  font-size: 42px

</style>


