<template>
  <def-layout>

    <div slot="layout_title">修改發票</div>

    <listing-update-top slot="layout_inner_head" 
      :_listings="listingContent" 
      :_select="select"
      >
    </listing-update-top>

    <!-- 表單主體 -->
    <listing-update-body slot="layout_inner_body"
      :_listingContent="listingContent"
      :_select="select"

      v-if="listingContent.length > 0"
    ></listing-update-body>

    <transition v-else name="fade" slot="layout_inner_body">
      <loading></loading>
    </transition>

    <!-- 底部插件 -->
    <listing-update-footer slot="layout_footer" 
      :the_prices="prices" 
      
      @pricesPdfView_Father="pricesPdfView"
      @pricesSubmit_Father="pricesSubmit" 
      @openFreightInsert_Father="openFreightInsert"
      ref="lpFooter"

      :numed_listing="numed"
      :timed_listing="timed"
      :payment_listing="payment"

      @updateTimed_Father="updateTimed"
      @updateNumed_Father="updateNumed"
      @updatePayment_Father="updatePayment"
      
      v-if="this.timed != ''"
    ></listing-update-footer>
    <loading-middle v-else slot="layout_footer" ></loading-middle>

    <!-- 其它插件 -->
    <div slot="layout_extra">
      <listing-choise-modal @returnChoised="reciverPrices" :defPrices="prices"></listing-choise-modal>
      <listing-update-full-msg></listing-update-full-msg>

      <listing-pdf-modal 
        :membery="prices.membery" 
        :_listing_content="selectContent" 

        :_numed="numed"
        :_timed="timed"
        :_payment="payment"

        :isPlus="true"
        @submit_Father="pricesSubmit"
        
        v-if="prices != null"
      >
        <span slot="buttonText">確認修改</span>
      </listing-pdf-modal>

      <custom-modal
        :id_prefix="'sureChangePrices'"
        :title="'提示'"
        :txt="'更改所屬報價單會造成發票的菜品內容被替換，是否繼續？'"
        @done_Father="openPriceChoise"
      >
        <div slot="submit_text">繼續</div>
      </custom-modal>

      <insert-freight-modal 
        :haved="listingContent" 
        ref="insertFreight"

        @reciverInsert="reciverInsertFreight"
      >
      </insert-freight-modal>
    
      <submit @submitConfirm="submit"></submit>
    </div>
  </def-layout>
</template>

<script>
import $ from 'jquery'
import view from '@/common/es/view'
import tool from '@/common/es/tool'
import validate from '@/common/es/validate'
import config from '@/common/config'

import conn from '@/service/conn'

import DefLayout from '@/components/Layout/DefLayout.vue'
import ListingUpdateTop from './Top/ListingUpdateTop.vue'
import ListingUpdateBody from './Body/ListingUpdateBody.vue'
import ListingUpdateFooter from './Footer/ListingUpdateFooter.vue'

import Loading from '@/components/Shimmer/Loading.vue'
import ListingChoiseModal from '@/components/Modal/Choise/ListingChoiseModal.vue'
import ListingUpdateFullMsg from './FullMsg/ListingUpdateFullMsg.vue'
import ListingPdfModal from '@/components/Modal/Pdf/ListingPdfModal.vue'
import Submit from '@/components/Modal/Submit.vue'
import CustomModal from '@/components/Modal/CustomModal.vue'
import InsertFreightModal from '@/components/Modal/Insert/InsertFreightModal.vue'

import LoadingMiddle from '@/components/Shimmer/LoadingMiddle.vue'

export default {
  name: 'ListingPlus',
  components: {
    DefLayout,
    ListingUpdateTop,
    ListingUpdateBody,
    ListingUpdateFooter,
    Loading,
    ListingChoiseModal,
    ListingUpdateFullMsg,
    ListingPdfModal,
    Submit,
    CustomModal,
    InsertFreightModal,
    LoadingMiddle
  },
  data() {
    return {
      listingContent: [],
      prices: null,
      select: [],
      selectContent: [],
      isLoading: true,

      timed: '',
      numed: '',
      payment: 0,
      result_price: '',

      listingContentId: null,
      listingId: 0
    }
  },
  mounted() {
    // this.loadPrices()
    this.listingContentId = this.$route.query.listingContentId
    this.loadUpdate(this.listingContentId)

    // this.loadContent()
    // this.fullMsg()
  },
  methods: {
    loadUpdate(id) {
      if ( (id != null) && (id != undefined)) {
        conn.listing_content.getOne(id).then(res => {
          
          if (conn.judge_response(res)) {
            this.listingContent = JSON.parse(res.content)
            this.select = this.listingContent.map(e => e.id)
            
            this.prices = res.listing.price_collect
            this.prices.membery = res.listing.membery

            this.numed = res.listing.num
            this.listingId = res.listing.id
            this.payment = res.listing.pay_way
            this.timed = tool.timed.ser_timed(res.listing.listing_time, false, true)
          }
        }).catch(err => {
          tool.showToast('網絡錯誤！！！')
        })
      } else {

        tool.showToast('未找到要修改的發票對象！！！', 2800)
        
        tool.closeAllModal()
        setTimeout(() => {
          this.$router.push('/home/listing')
        }, 700)
      }
    },
    loadPrices() {
      if (this.prices == null) {
        // const active_prices = tool.storage._get('listing_active_prices')

        if ((active_prices != null) && (active_prices != '') && (active_prices != undefined)) {
          this.prices = JSON.parse(active_prices)
        } else { this.openPriceChoise() }
        return false
      }
      return true
    },
    loadContent() {

      if ( this.prices != null ) {
        const condition = {
          price_collect: this.prices.id
        }
        
        conn.prices_content.getOneByPrices(condition).then(res => {

          if (conn.judge_response_list(res)) {
            
            const _lc = JSON.parse(res[0].content)

            this.listingContent = []
            for (const l in _lc) {
              
              this.listingContent.push({
                ..._lc[l],
                zk: '',
                count: '0.00',
                res: '0.00'
              })
            }
            this.select = [ this.listingContent[0].id ]

          }
          this.isLoading = false
          
        }).catch(err => {
          tool.showToast('網絡連接失敗。')
        })
      } else {
        tool.showModal('pricesChoiseModal')
      }
    },
    //
    openPriceChoise() {
      tool.showModal('pricesChoiseModal')
    },
    openFreightInsert() {
      this.$refs.insertFreight.openInsertFreight()
      this.$refs.insertFreight.loadHaventFreight()
    },
    //
    updateTimed(v) {
      this.timed = v
    },
    updateNumed(v) {
      this.numed = v
    },
    updatePayment(v) {
      this.payment = v
    },
    //
    reciverInsertFreight(v) {
      this.listingContent.unshift({
        ...v,
        zk: '',
        count: '1',
        res: '0.00'
      })
    },
    reciverPrices(v) { 
      this.prices = v 
      // tool.storage._set('listing_active_prices', JSON.stringify(v))
      this.loadContent()
      tool.showToast('列表加載完畢。', 1200)
    },
    collectSelect() {
      this.selectContent = this.listingContent.filter((e) => tool.inList(e.id, this.select))

      this.selectContent = this.selectContent.map((e) => {
        e.res = tool.floatNum.floatMul(e.count, e.price)
        return e
      })

      if (this.select.length > 1) {
        let res = 0
        for (const e of this.selectContent) {
          res = tool.floatNum.floatAdd(res, e.res)
        }
        this.result_price = res
      } else {
        this.result_price = this.selectContent[0].res
      }
    },
    collectMsg() {
      return true
      /*
      const res = this.$refs.lpFooter.validateInput()
      
      if (res != false) { 
        this.timed = res.timed
        this.numed = res.numed
        this.payment = res.payment
        return true
      }
      return false
      */
    },
    // 提交

    _submit(data) {
      const _this = this
      
      conn.listing.update(data, this.listingId).then(res => {
        
        if (conn.judge_response(res)) {
          
          conn.listing_content.update(
            {
              content: JSON.stringify(_this.selectContent)
            }, 
            this.listingContentId
          ).then(res => {

            if (conn.judge_response(res)) {

              tool.showToast('修改成功！！！', 1800)
              
              tool.closeAllModal()
              setTimeout(() => {
                this.$router.push('/home/listing')
              }, 700)
            }
          })
          
        }
      })
      
    },
    submit(contact) {
      
      const data = {
        num: this.numed,
        listing_time: this.timed + ' 00:00:00',
        pay_way: this.payment,
        // membery_id: this.prices.membery.id,
        // price_collect_id: this.prices.id,
        freight_item_num: this.select.length,
        total_price_freight: this.result_price,
        result_price: this.result_price,
        //
        pay_contact_area: contact.area,
        pay_contact_named: contact.contact_named,
        pay_contact_phone: contact.phoned,
        pay_contact_email: contact.email,
      }

      this._submit(data)
    },
    pricesSubmit(v) {
      
      const res = this.collectMsg()
      if (res) {
        this.collectSelect()

        const empty = this.selectContent.filter((e) => {
          if (e.res == 0) { return true } return false
        })

        if (empty.length > 0) { 
          tool.showToast(`存在 ${empty.length}項 數量為 0 的選項！！！`) 
        } 
        else { 
          // tool.showModal('submitModal')

          this.submit(v)
        }
      }
    },
    pricesPdfView() {
      const res = this.collectMsg()
      if (res) {
        this.collectSelect()
        tool.showModal('listingPdfViewModal')
      }
    },
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


